.posts {
	width: 100%;
	margin-top: -2rem;
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	// min-width: 300px
	grid-template-columns: repeat(auto-fill, minmax(360px,1fr)); }

.post-item {
	position: relative;
	flex: 1;
	background: white;
	transition: .3s;
	border-radius: 5px;
	margin: 0 .5rem;
	margin-bottom: 2rem;
	min-width: 300px;
	max-width: 500px;

	&:hover, &:focus, &:target {
		transform: translateY(-10px); }

	img {
		min-height: 300px;
		width: 100%;
		object-fit: cover; } }

@supports not (display: grid) {
  .post-item {}
		min-width: 300px; }

// For IE 11 and older
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
	.post-item {
		min-width: 300px; } }

.post-item__content {
	margin: 0 1rem;
	padding-bottom: 4rem; }

.post-title {
	margin-top: 1rem;
	letter-spacing: 1pt; }

.post-item__date {
  display: block;
  margin-top: -.5rem;
  margin-bottom: 1rem;
  color: #9a9a9a; }

.post-item__categories {
	position: absolute;
	bottom: 1rem;
	left: 1rem;
	font-size: .8rem;
	letter-spacing: 1px;
	color: #9a9a9a; }
