// Include original theme
@import 'addition/poole';
@import 'addition/hyde';
@import 'vars/syntax';

// Include MY Other css files
@import 'vars/vars';

// Include MY moduels
@import 'moduels/btn';
@import 'moduels/sidebar';
@import 'moduels/hero';
@import 'moduels/pagination';
@import 'moduels/post';
@import 'moduels/related';
@import 'moduels/footer';

.wrapper {
	display: flex; }

.wrapper__right {
	width: 100%; }

.content {
	margin-left: 10%; }

.title-box {
	margin: 2rem 0;
	margin-top: 3rem; }

.event {
	position: relative;
	z-index: 1;
	display: flex;
	flex-wrap: wrap; }

@media (max-width: 48rem) {
	.event {
		flex-direction: column; } }

.event-item {
	overflow: hidden;
	position: relative;
	cursor: pointer;
	flex: 1;
	margin: .3rem;
	min-height: 300px;
	min-width: 300px;

	img {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		z-index: -1; } }

// For Testing
.test {
	outline: 2px solid red; }

// Main
html {
	background: $gray-2; }

body {
	position: relative; }

ul {
	list-style-type: none;
	padding: 0;
	margin: 0; }


@media (max-width: 48rem) {
	body {
		margin-bottom: 50%; } }

a {
	color: inherit;
	&:hover, &:focus {
		text-decoration: none; } }

img {
	margin: 0;
	object-fit: cover; }

.theme-tri-dondon {
	background: $gray-2; }


.theme-tri-dondon .sidebar {
	background: $gray-2;
	color: $gray-9; }

.container-fluid {
	margin-left: 16rem;
	margin-right: 2rem;
	max-width: 1400px; }

@media (max-width: 48rem) {
	.container-fluid {
		margin-left: 0;
		margin-right: 0; } }

.content-fluid {
	margin-left: 20em;
	padding-top: 4rem;
	padding-bottom: 4rem; }

@media (min-width: 48em) {
	.content-fluid {
		background: red;
		max-width: 38rem;
		margin-left: 20rem;
		margin-right: 2rem; } }

@media (min-width: 64em) {
	.content-fluid {
		background: blue;
		margin-left: 22rem;
		margin-right: 4rem; } }
